.header-color{
  background: #16222A;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #3A6073, #16222A);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #3A6073, #16222A); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
}


/*---------------------Landing page-----------------------*/
.landing-grid{
  background-image: url(C:\Users\ggalv\Desktop\my-app\src\components\landing_fondo_grande.jpg);
  background-size: 1200px 800px;
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y:scroll
}

@media (max-width: 1000px){
  .landing-grid{
    background-image: url(C:\Users\ggalv\Desktop\my-app\src\components\landing_fondo_chica.jpg);
    background-size: 800px auto;
    background-position: center;
    text-align: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow-y:scroll
  }
  }

.avatar-img{
  height: 300px;
  padding-top: 5em;
}

.banner-text{
  background-color: black;
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1{
  font-size: 66px;
  font-weight: bold;
  color:white;
}
.banner-text hr{
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p{
  color:white;
  font-size: 20px;
  padding: 1em;
}

.social-links{
  display:  flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
}

.social-links i{
  color: white;
  font-size:  5em;
  padding: 8px;
}

/*---------------------Projects page-----------------------*/

.projects-grid{
  display: flex;
  overflow-y:scroll;
}

@media (max-width: 1000px){
  .projects-grid{
    height: auto;
    width: 100%;
  }

}

.content{
  display: auto;
}


/*---------------------Contact page-----------------------*/
.conctac-body{
  margin: auto;
  background: grey;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
  overflow-y:scroll;
  
}

.contact-grid{
  text-align: center;
  justify-content: center;
  width: 83%;
  height: auto;
  background: white;
  color: black;
  border: 3px solid red;
  margin-bottom: 20px;
}

.contact-grid h2 {
  font-family: 'Anton', sans-serif;
}

.contact-grid p{
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
}

.contact-grid hr{
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}

.contact-list i{
  font-size: 50px;
  padding-right: 2rem;
}

/*---------------------About Me page-----------------------*/
.aboutme-right-col{
  background: #27221F;
  color: white;
  padding-left: 10px;
}


